
*,
*::before,
*::after {
    box-sizing: border-box;
}

body,
h1,
li,
dl,
dd {
    margin: 0;
}

body {
    scroll-behavior: smooth;
    text-rendering: optimizeSpeed;
    line-height: 1;
}

img {
    max-width: 100%;
    display: block;
}
input,
button,
textarea,
select {
    font: inherit;
}